import fetchVideoDetail from '@/features/videos/video.server';
import { VideoDetailResponse } from '@/features/videos/video.type';

export default async function VideoDetailPage({ params }: { params: Promise<{ id: string }> }) {
  const { id } = await params; //分割代入(params.id → id)
  const video: VideoDetailResponse = await fetchVideoDetail(id);

  return <div>{/* {video._count} */}</div>;
}
